import EventEmitter from 'events';


class Engine extends EventEmitter {
    constructor(emitter, network, modules) {
        super();

        this.msg = "ICH BIN EIN BERLINER";

        this.emitter = emitter;
        this.network = network;
        this._available_modules = modules;

        this._modules = [];
        this._components = [];
        this._processors = [];
        this._actions = [];

        this.games = [];

        this._setEventListeners();
    }

    _setEventListeners() {
        this.emitter.on('connection', () => {
            this.network.emit('request', { type: 'modules' });
            this.network.emit('request', { type: 'games' });
            this.emitter.emit('ready');
        });

        this.emitter.on('data', (data) => {
            console.log('received data');
            console.log(data);
            console.log(this.msg);
            switch (data.type) {
                case 'modules':
                    console.log('Modules list received');
                    this.loadModules(data.data);
                    break;
                case 'games':
                    console.log('Games list received');
                    this.games = data.data;
                    break;
            }
        });

        this.emitter.on('gameJoined', (data) => {
            console.log('Game joined!');
        });
    }

    loadModules(modules) {
        console.log('Loading modules...');
        console.log(modules);
        modules.forEach(name => {
            let module = modules[name];
            this._modules.push(name);

            console.log(module);

            this._actions.push(...module.actions);
            this._components.push(...module.components);
            this._processors.push(...module.processors);
        })
    }
}

export default Engine;



